import useStore from "../store";
export default {
  inheritAttrs: false,
  emits: {
    "route-click": payload => !!payload.id,
    "route-details-click": payload => !!payload.id
  },
  data() {
    return {
      store: useStore(),
      columnDefs: [{
        headerName: "Имя",
        field: "name",
        flex: 1
      }, {
        headerName: "Кол-во остановок",
        headerTooltip: "Кол-во остановок",
        width: 150,
        valueGetter(params) {
          return params.data.stopIds.length;
        }
      }]
    };
  },
  computed: {
    rowData() {
      return this.store.routes;
    }
  },
  methods: {
    rowClicked(ev) {
      this.$emit("route-click", {
        id: ev.data.id
      });
    },
    getContextMenuItems(params) {
      return [{
        name: "Подробно",
        action: () => {
          this.$emit("route-details-click", {
            id: params.node.data.id
          });
        }
      }];
    }
  }
};